import React from "react";
import moment from 'moment';
import parseCSV from 'csv-parse';
import xlsx from 'xlsx';
import {
  ComposedChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  Scatter,
  ResponsiveContainer,
  ZAxis,
} from 'recharts';
import cs from "../../styles/cs";

// const data = [
//   { index: 10000, red: 1643, blue: 790 },
//   { index: 1666, red: 182, blue: 42 },
//   { index: 625, red: 56, blue: 11 },
//   // Calculation of line of best fit is not included in this demo
//   { index: 300, redLine: 0 },
//   { index: 10000, redLine: 1522 },
//   { index: 600, blueLine: 0 },
//   { index: 10000, blueLine: 678 },
// ];

const VTradeHistory = () => {
  const data = [
    { index: new Date('2021-03-01 09:00').getTime(), red: 1643, blue: 790 },
    { index: new Date('2021-03-01 09:03').getTime(), red: 182, blue: 42 },
    { index: new Date('2021-03-01 09:06').getTime(), red: 56, blue: 11 },
    // Calculation of line of best fit is not included in this demo
    { index: new Date('2021-03-01 09:03').getTime(), redLine: 0 },
    { index: new Date('2021-03-01 09:06').getTime(), redLine: 1522 },
    { index: new Date('2021-03-01 09:00').getTime(), blueLine: 0 },
    { index: new Date('2021-03-01 09:06').getTime(), blueLine: 678 },
  ];

  const [prices, setPrices] = React.useState([]);
  const [trades, setTrades] = React.useState([]);
  const _refPricesFileForm = React.useRef();
  const _refTradesFileForm = React.useRef();

  function _setPrices(e) {
    if (!e.target.files || !e.target.files.length) {
      _refPricesFileForm.current.reset();
      setPrices([]);
      return;
    }
    
    var reader = new FileReader();
    reader.onload = function(e) {
      // Use reader.result
      // console.log(reader.result)
      parseCSV(reader.result, {}, function (err, output) {
        setPrices(output.map((arr, i) => {
          const date = arr[0];
          let time = arr[1].padStart(5, '0');

          return ({
            dateTime: new Date(`${date} ${time}`).getTime(),
            price   : parseInt(arr[2]),
            rsi     : parseFloat(arr[3]),
            volumn  : parseInt(arr[4])
          });
        }));
      });
    }
    reader.readAsText(e.target.files[0]);
  }

  // serialDate is whole number of days since Dec 30, 1899
  // offsetUTC is -(24 - your timezone offset)
  // function serialDateToJSDate(serialDate, offsetUTC) {
  //   return new Date(Date.UTC(0, 0, serialDate, offsetUTC));
  // }
  
  function serialDateToJSDate(excelSerialDate) {
    return new Date(Date.UTC(0, 0, excelSerialDate - 1));
  }

  function upload(event) {
    var input = event.target; 
    var reader = new FileReader();
    reader.onload = function() { 
      var fdata = reader.result; 
      var read_buffer = xlsx.read(fdata, {type : 'binary'}); 
      read_buffer.SheetNames.forEach(function(sheetName) { 
        var rowdata =xlsx.utils.sheet_to_json(read_buffer.Sheets[sheetName]); 
        console.log(rowdata[0]['일자']);
        console.log(serialDateToJSDate(rowdata[0]['일자']).toLocaleDateString());
        console.log(JSON.stringify(rowdata)); 
      }) 
    }; 
    reader.readAsBinaryString(input.files[0]); 
  }

  function _setTrades(e) {
    if (!e.target.files || !e.target.files.length) {
      _refPricesFileForm.current.reset();
      setPrices([]);
      return;
    }
    
    var reader = new FileReader();
    reader.onload = function(e) {
      // Use reader.result
      // console.log(reader.result)
      parseCSV(reader.result, {}, function (err, output) {
        setPrices(prices.concat(output.map((arr, i) => {
          const date = arr[3];
          let time = arr[2].padStart(5, '0');

          let price = parseFloat(arr[1]);
          
          return ({
            dateTime: new Date(`${date} ${time}`).getTime(),
            [price >= 0 ? "buy" : "sell"] : parseInt(arr[0]),
            ea      : Math.abs(price),
          });
        })));
      });
    }
    reader.readAsText(e.target.files[0]);
  }

  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      const time = moment(label).format('YY-MM-DD HH:mm');
      const type = payload[0].name;
      const price = payload[0].value;
      const ea = payload[0].payload.ea;
      let color;
      if (type === 'SELL')
        color = 'blue';
      else if (type === 'BUY')
        color = 'red';

      return (
        <div className="bg-light p-2 border rounded">
          <p style={{fontSize: '8px'}}>{time}</p>
          <table>
            <tbody>
              <tr>
                <td><p style={{ color }}>{`${type}`}</p></td>
                <td><p style={{ color }}>{` : `}</p></td>
                <td><p style={{ color }}>{`${price}`}</p></td>
              </tr>
        
              {ea && (
                <tr>
                  <td><p>{`VOLUME`}</p></td>
                  <td><p>{` : `}</p></td>
                  <td><p>{`${ea}`}</p></td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      );
    }
  
    return null;
  };
  
  return ( 
    <div>
      <div>
        <button type="button" className="btn btn-primary" onClick={e => console.log(prices)}>Prices 테스트</button>
      </div>
      <div>
        <button type="button" className="btn btn-primary" onClick={e => console.log(trades)}>Trades 테스트</button>
      </div>
      <div>
        <form ref={_refPricesFileForm}>
          <input type="file" accept=".csv,.xlsx" id="pricesFileInput"
            className="d-none"
            onClick={e => e.target.key = 'prices' + new Date().getMilliseconds()}
            // name="pricesFile" onChange={_setPrices}
            name="pricesFile" onChange={upload}
          />
        </form>
        <label type="button" htmlFor="pricesFileInput" className="btn btn-primary">가격 파일 업로드</label>
      </div>
      <div>
        <form ref={_refTradesFileForm}>
          <input type="file" accept=".csv" id="tradesFileInput"
            className="d-none"
            onClick={e => e.target.key = 'trade' + new Date().getMilliseconds()}
            name="tradesFile" onChange={_setTrades}
          />
        </form>
        <label type="button" htmlFor="tradesFileInput" className="btn btn-primary">매매 파일 업로드</label>
      </div>

      <div style={{height: `${400}px`}}>
        <ResponsiveContainer width="100%" height="100%">
          <ComposedChart
            width={500}
            height={400}
            data={prices}
            margin={{
              top: 20,
              right: 80,
              bottom: 20,
              left: 20,
            }}
          >
            <CartesianGrid stroke="#f5f5f5" />
            <Tooltip content={<CustomTooltip />} />
            <Legend />

            <XAxis 
              dataKey="dateTime" 
              domain={['auto', 'auto']}
              type="number" 
              tickFormatter={(unixTime) => moment(unixTime).format('HH:mm Do')}
              label={{ value: 'TIME', position: 'insideBottomRight', offset: 0 }} 
            />
            <YAxis
              // dataKey="price" 
              domain={['auto', 'auto']}
              unit=""
              type="number"
              label={{ value: 'WON', angle: -90, position: 'insideLeft' }}
            />
            <ZAxis dataKey="ea" name="ea" range={[32, 288]} unit="ea" />
            {/* <Scatter name="red" dataKey="red" fill="red" /> */}
            <Line name="PRICE" dataKey="price" stroke="black" dot={false} activeDot={false} legendType="none" />
            <Scatter name="BUY"  dataKey="buy"  fill="red"  />
            <Scatter name="SELL" dataKey="sell" fill="blue" />
            {/* <Line dataKey="redLine" stroke="red" dot={false} activeDot={false} legendType="none" /> */}
          </ComposedChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
 
export default VTradeHistory;